.Navbar {
  width: 100%;
  height: var(--navbar-height);
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--navbar-bg-color);
  display: flex;
  justify-content: center;
  z-index: 300;
  transition: background-color 350ms ease ;
}

.Scrolled {
  background-color: var(--navbar-scrolled-bg-color);
}
